<template>
  <tr valign="top">
    <td class="company-ele">{{itemData.payerCompany}}</td>
    <td>
      <span class="block-span num-total" v-if="itemData.payAmount">
        付款总金额：<em class="price-num">￥ {{itemData.payAmount}}</em>
      </span>
      <div class="num-brief">
        <payPoper :settlementFundList="itemData.settlementFundList" v-if="itemData.settlementFundList"></payPoper>
      </div>
    </td>
    <td>
      <template v-if="itemData.orderList && itemData.orderList.length > 0">
        <span class="block-span num-total" v-if="itemData.orderAmount">订单总金额：<em class="price-num">￥ {{itemData.orderAmount}}</em><br></span>
        <div class="num-brief">
          <orderPopper :orderList="itemData.orderList"></orderPopper>
        </div>
      </template>
    </td>
    <td class="total-ele">
      <template v-for="opera in itemData.operates" v-if="opera.operateType && opera.amount">
        <span>{{opera.operateType}}<em class="price-num">￥ {{opera.amount}}</em> </span>
      </template>
    </td>
    <td>
      <span class="block-span">{{itemData.operateTime}}</span>
      {{itemData.operators}}
    </td>
  </tr>
</template>
<style>
</style>
<script>
  import payPoper from './paymentPopper.vue'
  import orderPopper from './ordersPopper.vue'
  export default{
    props: ['itemData'],
    data () {
      return {
        msg: 'hello vue'
      }
    },
    components: {
      payPoper,
      orderPopper
    }
  }
</script>



