import './mask.styl'
const doc = document
/* eslint-disable no-unused-vars */
const getWindowHeight = () => {
  var h = window.innerHeight ||
      doc.documentElement.clientHeight ||
      doc.body.clientHeight
  return h + 'px'
}
// 包含或等于自身
const contains = (a, b) => {
  if (a === b) { return true }
  return a.contains ? a !== b && a.contains(b) : (a.compareDocumentPosition(b) === 20)
}
class GuidMask {
  constructor (options) {
    this.__init(options)
  }
  __init (options) {
    let elestr = options.elestr
    let substr = options.substr
    this.closeCb = options.closeCb || null
    this.el = doc.querySelector(elestr)
    this.subel = this.el.querySelector(substr)
    this.__initDom()
    this.__setAlign()
    this.__animalUse(0)
    this.bindEle()
  }
  __initDom () {
    // 将元素追加到body中
  }
  __setAlign () {
    this.el.style.lineHeight = getWindowHeight()
  }
  bindEle () {
    let self = this
    window.addEventListener('resize', () => { self.__setAlign() })
    this.el.addEventListener('click', (e) => {
      let target = e.target || e.srcElement
      let subele = self.subel
      if (!contains(subele, target)) {
        self.close()
      }
    })
  }
  close () {
    let self = this
    this.__animalUse(300, () => {
      self.el.style.display = 'none'
      if (self.closeCb) {
        self.closeCb()
      }
    })
  }
  __animalUse (time, cb) {
    let ele = this.el
    ele.setAttribute('fe-state', 'close')
    setTimeout(() => {
      ele.removeAttribute('fe-state')
      if (cb) {
        cb()
      }
    }, time)
  }
}
export default GuidMask



