(function () {
  var myTest = window.myTest || (window.myTest = {})
  var toast = null
  var noop = function () {}
  // 检测是否支持classList
  var isSupportClassList = !!document.body.classList
  // dom操作
  var node = {
    hasClass: function (name) {
      if (!name) return false
      if (isSupportClassList) {
        return this.classList.contains(name)
      } else {
        return this.className.indexOf(name) > -1
      }
    },
    addClass: function (name) {
      if (!name) return false
      if (isSupportClassList) {
        this.classList.add(name)
      } else {
        if (!('className' in this)) return this
        if (!this.hasClass(name)) {
          this.className = this.className + ' ' + name
        }
      }
      return this
    },
    removeClass: function (name) {
      if (!name) return false
      if (isSupportClassList) {
        this.classList.remove(name)
      } else {
        if (!('className' in this)) return this
        if (this.hasClass(name)) {
          this.className = this.className.replace(name, '')
        }
      }
      return this
    }
  }
  /**
   * 打开toast
   * @param message 提示信息
   * @param duration 持续时间,默认1500
   * @param zindex 层叠水平,默认1
   * @param position 位置:top,center,bottom,默认center
   * @param autoClose 自动关闭,默认true
   * @param callback 关闭时的回调函数
   */
  myTest.openToast = function (message, duration, position, callback) {
    myTest.closeToast()
    if (toast) return
    var options
    var zindex = 1
    var icon = ''
    var size = ''
    var type = ''
    var title = ''
    if (typeof message === 'object') {
      options = message
      title = options.title
      message = options.message
      duration = options.duration
      position = options.position
      callback = options.callback
      zindex = options.zindex || zindex
      icon = options.icon || icon
      size = options.size || size
      type = options.type || type
      if (icon) {
        icon = ' icon-' + icon
      }
    }
    duration = duration || 1500
    callback = callback || noop
    toast = document.createElement('div')
    for (var key in node) {
      toast[key] = node[key]
    }
    toast.addClass('toast-container')
    position && toast.addClass(position)
    size && toast.addClass(size)
    toast.style.zIndex = zindex
    toast.innerHTML = '<div class="toast-wrapper"><div class="toast-box ' + size + ' ' + type + '"><div class="toast-icon' + icon + '"></div><div class="toast-message"><i>' + title + '</i>' + message + '</div></div></div>'
    document.body.appendChild(toast)
    // 这条语句不是多余,为添加动画作缓冲
    /* eslint-disable no-unused-expressions */
    toast.offsetHeight
    toast.addClass('active')
    setTimeout(function () {
      toast && toast.removeClass('active')
      setTimeout(function () {
        if (toast) {
          toast.parentNode.removeChild(toast)
          toast = null
        }
      }, 800)
      callback.call(toast)
    }, duration)
  }
  myTest.closeToast = function () {
    if (toast) {
      toast.parentNode.removeChild(toast)
      toast = null
    }
  }
})()



