import axios from 'axios'
export const htmlUrl = {
  accountInfo: '/settlementFund/seller'
}
const getAcountInfo = (cbObj) => {
  let params = {}
  let url = htmlUrl.accountInfo
  axios.get(url, {params: params}).then((resp) => {
    let data = resp.data.data
    if (data && cbObj) {
      cbObj.succ(data)
    }
  })
}

export const ajax = {
  getAcountInfo
}



