<template>
  <div class="mod-intelligent-dealed">
    <topTab activenum="2" ref="topTab">
      <div slot="main">
        1. 待手动核销的收款自来账日30天后，如您未主动处理，届时平台将自动退回。<a href="//help.myTest.com/payment/#&amp;sid=s1" target="_blank">更多细则详见&gt;&gt;</a><br>
        2. 以下信息是系统自动核销失败后待核销的收款信息处理情况，资金真实到账情况以线下银行通知为准。
      </div>
    </topTab>
    <interllFilter :company="companystr" @filterCb="filtercb" timeTit="处理时间"></interllFilter>
    <tableEle></tableEle>
    <guidMask v-if="showMask" @closecb="hideGuidMask"></guidMask>
    <div>{{test1}}</div>
    <div class="pager-wrapper">
      <pager
          :currentPage.sync="currentPage"
          @update:currentPage="goPageCb"
          :totalCount="totalCount"
          :pageSize="pageSize"> </pager>
      <loadingEle v-if="loading"></loadingEle>
    </div>
  </div>
</template>
<script>
  import 'components/reset.styl'
  import guidMask from '../../../components/intelligent-public/user-guide/index.vue'
  import topTab from 'components/intelligent-public/top-tab.vue'
  import tableEle from './components/table.vue'
  import interllFilter from 'components/intelligent-public/intelligent-filter.vue'
  import pager from '@wag/vue-page'
  import loadingEle from '../../../components/intelligent-public/loading.vue'
  /* eslint-disable no-unused-vars */
  import localStore from '../../../utils/storage'
  import { ajax } from '../../../components/intelligent-public/ajax'
//  import { boxObj } from './common/dialogBox'
//  /* global myTest */
  export default{
    data () {
      return {
        msg: 'look here',
        test1: this.$store.getters.test1,
        companystr: null,
        userId: '009999gggg',
        showMask: false,
        searchData: {
          startTime: null,
          endTime: null,
          payerCompany: '',
          pageNo: 1
        }
      }
    },
    components: {
      topTab,
      tableEle,
      interllFilter,
      guidMask,
      pager,
      loadingEle
    },
    computed: {
      totalCount () {
        return this.$store.state.totalCount
      },
      pageSize () {
        return this.$store.state.pageSize
      },
      currentPage: {
        get () {
          return this.$store.getters.curPage
        },
        set () {

        }
      },
      loading () {
        return this.$store.getters.loading
      }
    },
    created () {
      // myTest.openToast('投诉成功')
      // boxObj.overdue('sgdttt')
      let self = this
      ajax.getAcountInfo({succ: function (data) {
        console.log(data)
        self.$refs.topTab.changeAccount(data.accountNo)
      }})
    },
    mounted () {
      this.isShowMask()
      // this.companystr = 'beforestr'
      this.$store.dispatch('getDealedData', this.searchData)
    },
    methods: {
      filtercb (data) {
        console.log(data)
        this.searchData.startTime = data.startTime
        this.searchData.endTime = data.endTime
        this.searchData.payerCompany = data.company
        this.searchData.pageNo = 1

        this.$store.dispatch('getDealedData', this.searchData)
      },
      goPageCb (pageNo) {
        // console.log(page)
        this.searchData.pageNo = pageNo

        this.$store.dispatch('getDealedData', this.searchData)
      },
      isShowMask () {
        let guidMask = localStore.get('guidMask')
        if (!guidMask) {
          this.showMask = true
        }
      },
      hideGuidMask () {
        localStore.set('guidMask', 'has see')
      }
    }
  }
</script>




