<template>
  <div class="interll-filter-mod">
    <div class="interll-fillter-item">
      <span class="item-name">{{timeTit}}：</span>
      <dateTime v-model="startTime"></dateTime> -
      <dateTime v-model="endTime" :min-limit-date="minendStart"></dateTime>
    </div>
    <div class="interll-fillter-item">
      <span class="item-name">买家付款单位名称：</span>
      <input class="only-text" type="text" v-model="curCompany">
    </div>
    <button class="base-btn" @click="toFilter">查询</button>
  </div>
</template>
<style>
</style>
<script>
  import './intelligentFilter.styl'
  import dateTime from 'vue-date-time/src/datepicker.vue'
  export default{
    name: 'interll-filter',
    props: {
      company: {
        type: String,
        defualt: ''
      },
      timeTit: {
        type: String,
        default: '到账时间'
      }
    },
    data () {
      return {
        msg: 'hello vue',
        curCompany: this.company,
        startTime: null,
        endTime: null,
        // 结束时间的最小值
        minendStart: null,
        // 开始时间的最大值
        maxstartEnd: null
      }
    },
    components: {
      dateTime
    },
    methods: {
      toFilter () {
        console.log('click here')
        let data = {}
        data.company = this.curCompany
        data.startTime = this.startTime
        data.endTime = this.endTime
        this.$emit('filterCb', data)
      }
    },
    watch: {
      company (v) {
        this.curCompany = this.company
      },
      startTime (v) {
        this.minendStart = v
      }
    }
  }
</script>



