<template>
  <div class="private-guidmask-mod">
    <div class="private-guidmask-content animated zoomIn"
         @click="handlerFuc($event)" :class="[curClass]">
      <section class="width-sty1" :class="[(num == 1) ? '' : 'tonone']">
        <img src="//img.myTest.com/public/152645337761416629287_607_534.png" alt="">
        <button class="btns item1">查看新功能</button>
      </section>
      <section class="width-sty2" :class="[(num == 2) ? '' : 'tonone']">
        <img class="item-img1" src="//img.myTest.com/public/152645629962887790694_1048_172.png" alt="">
        <img class="item-img2" src="//img.myTest.com/public/152645803738632757812_546_293.png" alt="">
        <button class="btns item2">下一步</button>
      </section>
      <section class="width-sty3" :class="[(num == 3) ? '' : 'tonone']">
        <img src="//img.myTest.com/public/152646096514343531946_1065_252.png" alt="">
        <img class="item-img3" src="//img.myTest.com/public/152646096512549683063_477_406.png" alt="">
        <button class="btns item3">下一步</button>
      </section>
      <section class="width-sty1" :class="[(num == 4) ? '' : 'tonone']">
        <img src="//img.myTest.com/public/152646869715449437603_607_534.png" alt="">
        <button class="btns item4">查看批量功能</button>
        <button class="btns item5" actionstr="close">退出引导说明</button>
      </section>
      <section class="width-sty5" :class="[(num == 5) ? '' : 'tonone']">
        <img src="//img.myTest.com/public/152646880414836318577_1050_262.png" alt="">
        <img class="item-img4" src="//img.myTest.com/public/152646880438480321031_655_369.png" alt="">
        <button class="btns item6">下一步</button>
      </section>
      <section class="width-sty6" :class="[(num == 6) ? '' : 'tonone']">
        <img src="//img.myTest.com/public/152646976439878630274_1018_112.png" alt="">
        <img class="item-img5" src="//img.myTest.com/public/152646976460700061903_370_218.png" alt="">
        <button class="btns item7">下一步</button>
      </section>
      <section class="width-sty1" :class="[(num == 7) ? '' : 'tonone']">
        <img src="//img.myTest.com/public/152646869715449437603_607_534.png" alt="">
        <button class="btns item8" actionstr="close">退出引导说明</button>
      </section>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
  import './index.styl'
  import GuidMask from './mask'
  const imgArr = [
    '//img.myTest.com/public/152645337761416629287_607_534.png',
    '//img.myTest.com/public/152645629962887790694_1048_172.png',
    '//img.myTest.com/public/152645803738632757812_546_293.png',
    '//img.myTest.com/public/152646096514343531946_1065_252.png',
    '//img.myTest.com/public/152646096512549683063_477_406.png',
    '//img.myTest.com/public/152646869715449437603_607_534.png',
    '//img.myTest.com/public/152646880438480321031_655_369.png',
    '//img.myTest.com/public/152646976460700061903_370_218.png',
    '//img.myTest.com/public/152646869715449437603_607_534.png'

  ]
  const maxSectLen = 7
  export default{
    data () {
      return {
        msg: 'hello vue',
        curClass: 'parent-step7',
        mask: null,
        num: 1
      }
    },
    components: {},
    mounted () {
      let self = this
      this.mask = new GuidMask({
        elestr: '.private-guidmask-mod',
        substr: '.private-guidmask-content',
        closeCb: () => {
          self.$emit('closecb')
        }
      })
      this.preload()
    },
    methods: {
      next () {
        this.num += 1
        this.curClass = 'parent-step' + this.num
        if (this.num > maxSectLen) {
          this.close()
        }
      },
      close () {
        this.mask.close()
        this.$emit('closecb')
      },
      handlerFuc (e) {
        let self = this
        let target = e.target || e.srcElement
        let actionstr = target.getAttribute('actionstr')
        switch (actionstr) {
          case 'next':
            // self.next()
            break
          case 'close':
            self.close()
            break
          default:
            self.next()
        }
      },
      preload () {
        for (let i = 0, len = imgArr.length; i < len; i++) {
          let img = new Image()
          img.onload = function () {}
          img.src = imgArr[i]
        }
      }
    }
  }
</script>



