import {ajax} from '../common/ajax'
export const module = {
  state: {
    curPage: 1,
    totalCount: 0,
    pageSize: 10,
    payerCompanyList: [],
    loading: false
  },
  mutations: {
    testAdd: (state, data) => {
      state.test1 += data
    },
    initData: (state, data) => {
      state.curPage = data.page
      state.totalCount = data.count
      state.pageSize = data.pageSize
      state.payerCompanyList = data.model
    }
  },
  actions: {
    func1: ({commit, state, dispatch}) => {
    },
    getDealedData: ({commit, state, dispatch}, param) => {
      state.loading = true
      ajax.getDealedList(commit, param, {succ: () => {
        state.loading = false
      },
      err: () => {
        state.loading = false
      }})
    }
  },
  getters: {
    test1: state => {
      return state.test1
    },
    payerCompanyList: state => {
      return state.payerCompanyList
    },
    curPage: state => {
      return state.curPage
    },
    loading: state => {
      return state.loading
    },
    totalCount: state => {
      return state.totalCount
    }
  }
}



