<template>
  <div>
  <VueTooltip :isShow.sync="isShow" target="click"
              placement="bottom-start"
              :modifiers="{
                computeStyle: {
                  gpuAcceleration: false
                }
              }"
              :class="[activeClass ? 'active-popper': '']"
              content="提示bottom-start, 能看到吗？ yes or no">
    <em class="popper-tit">共 {{settlementFundList.length}} 笔付款 ></em>
    <template slot="content">
      <div class="payacounts-parent">
          <div class="popper-header">
            <span class="time">到账时间</span>
            <div class="info">付款信息</div>
          </div>
          <div class="popper-content"  v-for="item in settlementFundList">
            <section class="item">
              <span class="time">{{item.transferTime}}</span>
              <div class="info">
                <p>金额：￥{{item.amount}}</p>
                <p>付款银行：{{item.payerBankName}}</p>
                <p>付款账号：{{item.payerCardNo}}</p>
                <p>网商银行流水号：{{item.bankFundId}}</p>
              </div>
            </section>
          </div>
      </div>
    </template>
  </VueTooltip>
  </div>
</template>
<style>
</style>
<script>
  export default{
    props: ['settlementFundList'],
    data () {
      return {
        msg: 'hello vue',
        isShow: false,
        activeClass: false
      }
    },
    components: {
    },
    watch: {
      isShow (val) {
        if (val) {
          this.activeClass = true
        } else {
          this.activeClass = false
        }
      }
    }
  }
</script>



