<template>
  <div>
  <VueTooltip :isShow.sync="isShow" target="click"
              placement="bottom-start"
              :modifiers="{
                computeStyle: {
                  gpuAcceleration: false
                }
              }"
              :class="[activeClass ? 'active-popper': '']"
              content="提示bottom-start, 能看到吗？ yes or no">
    <em class="popper-tit">共 {{orderList.length}} 笔订单 ></em>
    <template slot="content">
      <div class="orderinfo-popper-parent">
          <div class="popper-header">
            <span class="order-id">订单编号</span>
            <span class="order-price">订单金额</span>
          </div>
          <div class="popper-content">
            <section class="item" v-for="item in orderList">
              <span class="order-id">{{item.orderId}} </span>
              <span class="order-price">￥{{item.amount}}</span>
            </section>
          </div>
      </div>
    </template>
  </VueTooltip>
  </div>
</template>
<style>
</style>
<script>
  export default{
    props: ['orderList'],
    data () {
      return {
        msg: 'hello vue',
        isShow: false,
        activeClass: false
      }
    },
    components: {
    },
    watch: {
      isShow (val) {
        if (val) {
          this.activeClass = true
        } else {
          this.activeClass = false
        }
      }
    }
  }
</script>



